import { UseApiOptions, useRequest } from '../useRequest';
import {dataRequest, DataResponse} from "../types/query";

const baseURL = 'http://110.42.179.122:8000/api';
// const baseURL = 'http://localhost:8000/api';

export const useListData = (data?: dataRequest, options?: UseApiOptions) => {
	return useRequest<DataResponse>(
		{
			url: '/list',
			method: 'POST',
			data,
			baseURL,
		},
		options
	);
};
