import React from 'react';
import "@arco-design/web-react/dist/css/arco.css";
import styled from "styled-components";
import {Button, Form, Input, Notification, Space, Table} from "@arco-design/web-react";
import {AreaCascader} from "./AreaCascader";
import {useListData} from "./api/service/base";
import {Data, dataRequest} from "./api/types/query";
import {ProvincesCitiesMap} from "./AreaCascader/provinces_cities_new";

const FormItem = Form.Item

function App() {
	const [data, setData] = React.useState<Data[]>();

	const { loading,run} = useListData()

	const handleCopy = async (text: Data) => {
		try {
			await navigator.clipboard.writeText(Object.values(text).join(','));
			Notification.success({content: "复制成功"});
		} catch (err) {
			Notification.success({content: "复制失败"});
			console.error('Failed to copy: ', err);
		}
	};

	const columns =  [
		{
			title: '序号',
			render: (_text: unknown, _record: Data, index: number) => index + 1,
			width: 50,
		},
		{
			title: '点1名称',
      dataIndex: 'name1',
			width: 200,
		},
		{
			title: '点1经纬度',
      dataIndex: 'lat_long',
			width: 200
		},
		{
			title: '点1高德ID',
      dataIndex: 'amap_id',
			width: 100
		},
		{
      title: '点1地址',
      dataIndex: 'address',
			width: 300
    },
		{
			title: "附近关键词2存在数",
			dataIndex: "keyword2_count",
			width: 100
		},
		{
			title: "点2名称",
      dataIndex: "name2",
			render: (text: string[]) => text.join(' | '),
			width: 300
		},
		{
			title: "操作",
			render: (_text: unknown, record: Data) => (
				<Space>
					<Button type="text" onClick={() => handleCopy(record)}>复制</Button>
					<a href={record.amap_link} target="_blank" rel="noreferrer" >高德</a>
					<a href={record.baidu_link} target="_blank" rel="noreferrer" >百度</a>
				</Space>
			),
			width:160,
			fixed: 'right' as 'right' | 'left',
		}
	]

	const handleSubmit = async (values: dataRequest) => {
		const regions = values?.regions?.map(item => item.slice(-1)[0])
		values.regions = Array.from(new Set(regions?.map(v => ProvincesCitiesMap[v])));
		const res = await run({data: values} )
		if (res?.metadata?.code === 'ok') {
			setData(res?.result)
		}
	}

	return (
		<Wrapper>
			<Form onSubmit={handleSubmit} >
				<Header>
					<Space>
						<FormItem field="regions" noStyle>
							<AreaCascader placeholder="请选择地区" mode="multiple" style={{width: 240}} allowClear/>
						</FormItem>
						<FormItem field="keyword1" rules={[{required: true}]} noStyle>
							<Input placeholder="请输入关键词1"/>
						</FormItem>
						<FormItem field="type1" noStyle>
							<Input placeholder="请输入type1"/>
						</FormItem>
						<FormItem field="keyword2" noStyle>
							<Input placeholder="请输入关键词2"/>
						</FormItem>
						<FormItem field="type2" noStyle>
							<Input placeholder="请输入type2"/>
						</FormItem>
						<FormItem field="radius" noStyle>
							<Input placeholder="请输入关键词3"/>
						</FormItem>
					</Space>
					<Space>
						<Button type="primary" loading={loading} htmlType="submit">点击查询</Button>
					</Space>
				</Header>
			</Form>
			<Body>
				<Table  columns={columns} data={data} loading={loading} />
			</Body>
		</Wrapper>
	);
}

const Header = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    background-color: #fff;
`
const Body = styled.div`
	margin-top: 32px;
`
const Wrapper = styled.div`
    width: 80%;
    margin: 16px auto;

`

export default App;
